:root{
    --theme-color: #1DB954;
    --font-color: #34495e;
}

html{
    scroll-behavior: smooth;
}

body{
    font-family: Source Sans Pro,Helvetica Neue,Arial,sans-serif;
    color: var(--font-color);
    font-size: 15px;
}

.header{
    background: #222;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.header a{
    color: var(--theme-color);
}

.header-link{
    color: #34495e;
    text-decoration: none;
    margin-left: 20px;
}

.header-link:hover{
    color: var(--theme-color);
}

.active{
    color: var(--theme-color)!important;
    border-bottom: 2px solid var(--theme-color);
}

.cover{
    text-align: center;
    margin-top: 20vh;
    margin-bottom: 5vh;
}

.footer{
    font-family: Alata;
    background: #18181d;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    border-radius: 0;
    padding: 40px;
    color: #888;
}

.footer p{
    color: 10px;
}

div a{
    color: var(--theme-color);
    text-decoration: none;
}

pre{
    padding: 20px;
    background: #222;
    border-radius: 5px;
    color: #ddd;
    overflow: auto;
}

.home-content{
    margin-top: 400px;
    margin-bottom: 15px;
    padding: 30px;
}

.bottom-links{
    padding-top: 30px;
    text-align: center;
}

.bottom-links a:last-child{
    color: white!important;
    background: var(--theme-color)!important;
}

.bottom-links a{
    color: var(--theme-color);
    background: white;
    border: 1px solid var(--theme-color);
    padding: .75em 2rem;
    border-radius: 3px;
    margin-left: 5px;
    font-family: Alata;
    border-radius: 40px;
}

#doc-sidebar{
    position: fixed;
    top: 40px;
    left: 0;
    width: 270px;
    height: 100vh;
    padding-left: 40px;
    overflow: auto;
    background: #f7f7f7;
    z-index: 4;
    transition: 0.3s;
    display: block;
}

#doc-section{
    height: 100vh;
    position: absolute;
    right: 0;
    padding: 40px;
    transition: 0.3s;
}

#nav-btn{
    color: var(--theme-color);
    position: fixed;
    bottom: 20px;
    left: 20px;
    z-index: 10;
    cursor: pointer;
}

.header{
    z-index: 5;
    position: fixed;
}

td{
    border: none!important;
    color: black;
    padding: 8px;
}

tr{
    border: none!important;
}

tr:nth-child(odd) {
    background-color: #f9f9f9;
}

tr:nth-child(even) {
    background-color: #f4f4f4!important;
}

table{
    border-color: 1px solid #e2e2e2!important;
    border-spacing: 0;
    overflow: auto;
}

th{
    padding: 25px!important;
    font-family: Alata!important;
    border: none!important;
    background-color: #1DB954;
    color: white;
}

td{
    padding: 25px!important;
}

blockquote{
    border-left: 2px solid #a8b4ae!important;
    transition: 0.3s;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 0;
}

blockquote:hover{
    border-left: 4px solid #1DB954!important;
}

.light{
    font-family: consolas;
    font-weight: lighter;
}

hr{
    margin-top: 30px;
    border: none;
    border-bottom: 1px solid rgb(218, 218, 218);
}

h2{
    font-size: 1.72rem;
}

h3{
    font-size: 1.5rem;
}

#doc-section h3{
    margin-top: 35px;
}

.sidebar-active{
    border-left: 2px solid #1DB954!important;
    background: #ececec;
    border-radius: 3px;
    font-weight: bolder;
}

.sidebar-section a{
    padding: 0 9px;
    color: var(--font-color); 
    padding-right: 300px!important;
    white-space: nowrap;
}

.sidebar-section a:hover:not(.sidebar-heads){
    border-left: 2px solid #1DB954!important;
    background: #ececec;
    border-radius: 3px;
    font-weight: bolder;
}

.sidebar-heads{
    font-weight: 700;
    font-size: 1rem;
    font-family: Roboto,Source Sans Pro,Calibri,Arial,sans-serif;
}

::selection{
    color: white;
    background-color: #1DB954;
}

::-webkit-scrollbar-thumb{
    background: var(--theme-color);
}

::-webkit-scrollbar-track{
    background: #a8b4ae;
}

::-webkit-scrollbar{
    width: 7px;
}

.cover-title{
    font-weight: bolder!important;
    font-size: 100px;
    margin-top: -30px!important;
}

@media (max-width: 700px){

    .cover-title{
        font-size: 50px!important;
    }

    .home-content{
        margin-top: 300px;
    }

}

.cover-description{
    margin-top: -30px!important;
}

#scroll-up{
    position: fixed;
    bottom: 20px;
    right: 20px;
    border-radius: 3px;
    background-color: var(--theme-color);
    color: white;
    text-align: center;
    vertical-align: center;
    display: none;
    padding: 10px;
    font-weight: bold;
}

#search-box{
    outline: none;
    border: none;
    width: 300px;
    border-bottom: 3px solid var(--theme-color);
    font-weight: bolder;
    height: 30px;
}

.search-result-box{
    margin-top: 10px;
    white-space: nowrap;
    display: flex;
}

.search-result-box a{
    margin-left: 10px;
}

.type-block{
    color: white;
    border-radius: 3px;
    font-weight: bolder;
    font-weight: 700;
    font-size: 1rem;
    font-family: Roboto,Source Sans Pro,Calibri,Arial,sans-serif;
    text-align: center;
    width: 20px;
    height: 20px;
}